<style lang="less" scoped src='./index.less'>
</style>
<style lang="less">
body {
  background-color: #fafafc;
}
</style>
<template>
  <div class='ruleSetting'>
    <div class='ruleSetting-box'>
      <div class='title'>
        <span class='btn'>
          <el-button type="primary" v-if="operationState!=3" @click='submit'></el-button>
          <el-button type="primary" @click='cancel'></el-button>
        </span>
      </div>
      <div v-if="broker_type == 1">
        <div class='infoform'>
          <div class='text'></div>
          <el-form :model="form" :disabled="operationState==3" :rules="rules" ref="form">
            <el-form-item prop="property_type">
              <el-checkbox v-model="form.property_type" v-for="item in typeOptions" :key="item.param_id" :label="item.param">{{item.param}}</el-checkbox>
            </el-form-item>
            <el-form-item label="" class='input' prop="commission_way">
              <el-select clearable placeholder="" v-model="form.commission_way">
                <el-option v-for="item in commissionWayOption" :key="item.param_id" :label="item.param" :value="item.param_id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" class='input' prop="money_type">
              <el-select clearable placeholder="" v-model="form.money_type">
                <el-option v-for="item in moneyTypeOption" :key="item.param_id" :label="item.param" :value="item.param_id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" class='input' prop="param">
              <el-input v-model="form.param"></el-input>
            </el-form-item>
            <el-form-item label="" class='input'>
              <el-radio-group v-model="form.is_total">
                <el-radio :label="1"></el-radio>
                <el-radio :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="" class='input2'>
              <el-radio-group v-model="form.is_jump">
                <el-radio :label="1"></el-radio>
                <el-radio :label="0"></el-radio>
              </el-radio-group>
            </el-form-item>
          </el-form>
        </div>
        <div class='info' v-if="form.rule_id && form.is_jump == 1">
          <div class='title'>
            <div class='text'></div>
            <span class='btn'>
              <el-button type="primary" v-if="operationState!=3" @click="showAddJumpRule()"></el-button>
            </span>
          </div>
          <el-table :data="refund" border>
            <el-table-column label="" align='center' width="70px">
              <template slot-scope="scope">{{getIndex(scope)}}</template>
            </el-table-column>
            <el-table-column property="start" label="" align='center'></el-table-column>
            <el-table-column property="end" label="" align='center'></el-table-column>
            <!-- <el-table-column property="" label="" align='center'></el-table-column> -->
            <el-table-column property="value" label="" align='center'></el-table-column>
            <el-table-column label="" align='center'>
              <template slot-scope="scope">{{ scope.row.is_include == 1 ? ' ' : '' }}</template>
            </el-table-column>
            <el-table-column property="reward" label="/" align='center'></el-table-column>
            <el-table-column label="" align='center' v-if="operationState!=3">
              <template slot-scope="scope">
                <el-button type="text" @click="showAddJumpRule(scope.row , true)"></el-button>
                <el-button type="text" @click="showAddJumpRule(scope.row)"></el-button>
                <el-button type="text" @click="removeJumpRule(scope.row)"></el-button>
              </template>
            </el-table-column>
          </el-table>
          <el-dialog title="" :visible.sync="addJumpRuleDialog" @close="addJumpRuleDialog=false">
            <el-form :model="addJumpRuleForm" :disabled="jumpRuleIsDisabled" :rules="rules" ref="addJumpRuleForm">
              <el-form-item label="" class='input11' prop="start">
                <el-input v-model="addJumpRuleForm.start" />
              </el-form-item>
              <el-form-item label="" class='input11' prop="end">
                <el-input v-model="addJumpRuleForm.end" />
              </el-form-item>
              <el-form-item label="" class='input11' prop="value">
                <el-input v-model="addJumpRuleForm.value" />
              </el-form-item>
              <el-form-item label="/" class='input' prop="reward">
                <el-input v-model="addJumpRuleForm.reward" />
              </el-form-item>
              <el-form-item label="" class='input11'>
                <el-radio-group v-model="addJumpRuleForm.is_include">
                  <el-radio :label="1"></el-radio>
                  <el-radio :label="0"></el-radio>
                </el-radio-group>
              </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer" v-if="!jumpRuleIsDisabled">
              <el-button @click="addJumpRuleDialog=false"> </el-button>
              <el-button type="primary" @click="addJumpRuleSubmit"> </el-button>
            </div>
          </el-dialog>
        </div>
      </div>
      <!--    -->
      <div v-if="broker_type != 1">
        <div class='infoform'>
          <div class='text'></div>
          <el-form :model="form" :disabled="operationState==3" :rules="rules" ref="form">
            <el-form-item label="" class='input' prop="money_type">
              <el-select clearable placeholder="" v-model="form.money_type">
                <el-option v-for="item in moneyTypeOption" :key="item.param_id" :label="item.param" :value="item.param_id"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item label="" class='input' prop="param">
              <el-input v-model="form.param"></el-input>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>
  </div>

</template>
<script>
export default {
  data() {
    return {
      rules: {
        start: [
          {
            required: true,
            message: "",
            change: "change",
            pattern: /^[0-9]+(.[0-9]{0})?$/
          }
        ],
        end: [
          {
            required: true,
            message: "",
            change: "change",
            pattern: /^[0-9]+(.[0-9]{0})?$/
          }
        ],
        value: [
          {
            required: true,
            message: "2",
            change: "change",
            pattern: /^[0-9]+(.[0-9]{1,2})?$/
          }
        ],
        reward: [
          {
            required: true,
            message: "/",
            change: "change",
            pattern: /^[0-9]+(.[0-9]{1,2})?$/
          }
        ],

        property_type: [
          {
            type: "array",
            required: true,
            message: "",
            change: "change"
          }
        ],
        commission_way: [
          {
            type: "number",
            required: true,
            message: "",
            change: "change"
          }
        ],
        money_type: [
          {
            type: "number",
            required: true,
            message: "",
            change: "change"
          }
        ],
        param: [
          {
            required: true,
            message: "",
            change: "change",
            pattern: /^[0-9]+(.[0-9]{0})?$/
          }
        ]
      },
      refund: [],
      form: {
        rule_id: "",
        property_type: [],
        is_jump: 0,
        is_total: 0,
        money_type: "",
        commission_way: "",
        param: ""
      },
      value: "",
      typeOptions: [],
      commissionWayOption: [],
      moneyTypeOption: [],
      distribution: {},
      distributionDetail: {},
      broker_type: 0,
      isAddJumpRule: true,
      addJumpRuleForm: {
        rule_id: "",
        start: "",
        end: "",
        value: "",
        reward: "",
        is_include: 0
      },
      addJumpRuleDialog: false,
      jumpRuleIsDisabled: false,
      operationState: 1
    };
  },
  mounted() {
    this.distribution = this.$store.state.distribution.distribution;
    if (!this.distribution.project_id) {
      this.$router.push({ name: "distribution" });
      return;
    }
    this.broker_type = this.$route.query.broker_type;
    this.operationState = this.$route.query.operationState;
    this.distributionDetail = this.$store.state.distribution.detail;
    if (this.$route.query.rule_id) {
      this.form.rule_id = this.$route.query.rule_id;
      this.form.is_jump = 1;
      //
      this.getRuleInfo();
      //
      this.getJumpRuleList();
    }
    this.getMoneyType();
    this.getCommissionWay();
    this.getType();
  },
  methods: {
    getIndex(row) {
      let index = row.$index + 1;
      return index;
    },
    //
    async getRuleInfo() {
      let res = await this.api.getUpdateBrokeRule({
        rule_id: this.form.rule_id
      });
      if (res.code == 200) {
        let temp = res.data;
        console.log(temp);
        temp.property_type = temp.property_type.split(",");
        Object.assign(this.form, temp);
      }
    },
    //
    submit() {
      this.$refs["form"].validate(async valid => {
        if (valid) {
          // 
          if (this.broker_type != 1) {
            this.submitOtherRule();
            return;
          }
          let temp = {};
          temp.property_type = this.form.property_type.join(",");
          temp.company_rule_id = this.distribution.rule_id;
          temp.broker_type = this.broker_type;
          temp.is_jump = this.form.is_jump;
          temp.is_total = this.form.is_total;
          temp.money_type = this.form.money_type;
          temp.commission_way = this.form.commission_way;
          temp.param = this.form.param;
          if (!this.form.rule_id) {
            let res = await this.api.addBrokeRule(temp);
            if (res.code == 200) {
              this.$message({ type: "success", message: "" });
              this.form.rule_id = res.data;
            }
          } else {
            temp.rule_id = this.form.rule_id;
            let res = await this.api.updateBrokeRule(temp);
            if (res.code == 200) {
              this.$message({ type: "success", message: "" });
            }
          }
        } else {
          return false;
        }
      });
    },
    async submitOtherRule() {
      let temp = {};
      temp.company_rule_id = this.distribution.rule_id;
      temp.broker_type = this.broker_type;
      temp.money_type = this.form.money_type;
      temp.param = this.form.param;
      if (!this.form.rule_id) {
        let res = await this.api.addBrokeRule(temp);
        if (res.code == 200) {
          this.$message({ type: "success", message: "" });
          this.form.rule_id = res.data;
        }
      } else {
        temp.rule_id = this.form.rule_id;
        let res = await this.api.updateBrokeRule(temp);
        if (res.code == 200) {
          this.$message({ type: "success", message: "" });
        }
      }
      this.$router.push({ name: "ruleOfMaid" });
    },
    async getJumpRuleList() {
      let res = await this.api.getJumpRuleList({
        rule_id: this.form.rule_id
      });
      if (res.code == 200) {
        this.refund = res.data;
      }
    },
    async getCommissionWay() {
      let res = await this.api.getCommissionWay();
      if (res.code == 200) {
        this.commissionWayOption = res.data;
      }
    },
    async getMoneyType() {
      let res = await this.api.getMoneyType();
      if (res.code == 200) {
        this.moneyTypeOption = res.data;
      }
    },
    async getType() {
      if (this.broker_type != 1) {
        return;
      }
      let res = await this.api.getTags();
      if (res.code == 200) {
        let tempArr = res.data;
        let totalTypes = [];
        for (let tag of this.distributionDetail.property_tags) {
          let text = "";
          for (let temp of tempArr) {
            if (temp.param_id == tag) {
              totalTypes.push(temp);
            }
          }
        }
        for (let temp of totalTypes) {
          if (
            this.distribution.property_type &&
            this.distribution.property_type.includes(temp.param)
          ) {
          } else {
            this.typeOptions.push(temp);
          }
        }
        if (this.typeOptions.length == 0) {
          this.$message({
            type: "error",
            message: ""
          });
          this.$router.push({
            name: "ruleOfMaid"
          });
        }
      }
    },
    cancel() {
      this.$router.back();
    },
    showAddJumpRule(item, isDiabled) {
      this.jumpRuleIsDisabled = isDiabled;
      if (item) {
        this.isAddJumpRule = false;
        Object.assign(this.addJumpRuleForm, item);
      } else {
        this.isAddJumpRule = true;
        Object.assign(
          this.addJumpRuleForm,
          this.$options.data()["addJumpRuleForm"]
        );
      }
      this.addJumpRuleDialog = true;
    },

    addJumpRuleSubmit() {
      this.$refs["addJumpRuleForm"].validate(async valid => {
        if (valid) {
          this.addJumpRuleForm.rule_id = this.form.rule_id;
          let result = {};
          if (this.isAddJumpRule) {
            result = await this.api.addJumpRule(this.addJumpRuleForm);
          } else {
            result = await this.api.updateJumpRule(this.addJumpRuleForm);
          }
          if (result.code == 200) {
            this.$message({ type: "success", message: "" });
          }
          this.addJumpRuleDialog = false;
          this.getJumpRuleList();
        } else {
          return false;
        }
      });
    },
    removeJumpRule(item) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(async () => {
          let temp = {};
          temp.jump_id = item.jump_id;
          temp.rule_id = this.form.rule_id;
          let res = await this.api.deleteJumpRule(temp);
          if (res.code == 200) {
            this.$message({
              type: "success",
              message: "!"
            });
          }
          this.getJumpRuleList();
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>



